.footer {
  border-radius: 8px;
  background-color: black;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 70px;
  padding: 20px 25px;
}
.footer .footer_left > a > .MuiSvgIcon-root {
  color: white !important;
  font-size: 23px !important;
  margin-right: 6px;
}
.footer .footer_left > a > .MuiSvgIcon-root:hover {
  color: var(--main-color);
}
.footer .footer_copyryt_text {
  font-size: small;
  color: #9c9c9c !important;
  text-align: end;
}
.footer .footer_copyryt_text > a {
  color: var(--main-color);
  text-decoration: none !important;
}
